:root {
  --black-alpha-05: rgb(0, 0, 0, 0.05);
  --black-alpha-10: rgb(0, 0, 0, 0.1);
  --black-alpha-15: rgb(0, 0, 0, 0.15);
  --black-alpha-20: rgb(0, 0, 0, 0.2);
  --black-alpha-30: rgb(0, 0, 0, 0.3);
  --black-alpha-40: rgb(0, 0, 0, 0.4);
  --black-alpha-50: rgb(0, 0, 0, 0.5);
  --black-alpha-60: rgb(0, 0, 0, 0.6);
  --black-alpha-80: rgb(0, 0, 0, 0.8);
  --white: #fff;
  --deep-blue: #1467b3;
  --navy-blue: #2b94f5;
  --sky-blue: #389fff;
  --light-blue: #eaf5ff;
  --yellow: #ffe39d;
  --gray: #d8d8d8;
  --light-gray: #f2f2f2;
  --black: #000;
  --light-black: rgba(0, 0, 0, 0.8);
  --gray-border: #97979766;
  --green: #037701;
  --red: #de100d;
  --purple: #a55ae9;
  --pink: #c93485;
  --brown: #b74319;
  --indigo: #1700ff;
  --background-blue: #ecedf3;
}

::-webkit-input-placeholder {
  color: #000;
  color: var(--black);
  opacity: 1;
  /* Firefox */
}

:-ms-input-placeholder {
  color: #000;
  color: var(--black);
  opacity: 1;
  /* Firefox */
}

::placeholder {
  color: #000;
  color: var(--black);
  opacity: 1;
  /* Firefox */
}

:-ms-input-placeholder {
