}
.navBar {
    width: 100%;
    box-shadow: rgba(0, 0, 0, 0.2) 0 3px 5px;
    background-color: #fff;
    position: fixed;
    z-index: 1600;
    transition: top 0.3s;
}

.navContent {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    /* padding: 10px 0; */
}

.responsiveNav {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    transition: right 1s;
}

.linkList {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.linkList div {
    display: inline-block;
    color: var(--black);
    padding: 0 30px;
    height: 60px;
    line-height: 60px;
    font-size: 18px;
    margin-left: 30px;
    cursor: pointer;
    border-radius: 5px;
}

.linkList div:not(:first-child) {
    margin-left: 0;
}

.linkList div:hover {
    background-color: var(--black-alpha-05);
}

.logo {
    cursor: pointer;
    height: 100%;
}

.navRight {
    width: 450px;
    -webkit-align-self: flex-end;
            align-self: flex-end;
}

.navFeature {
    display: -webkit-flex;
