}

.searchBarContainer {
    display: -webkit-flex;
    display: flex;
}

.searchBar {
    display: block;
    width: 240px;
    height: 40px;
    border-radius: 0 5px 5px 0;
    line-height: 40px;
    background-repeat: no-repeat;
    padding-left: 20px;
    color: var(--black);
    font-size: 16px;
    letter-spacing: 0.8px;
    box-sizing: border-box;
    background-color: var(--light-gray);
    border: 2px solid var(--light-gray);
    transition: border .2s ease-in, background-color .2s ease-in
}

.searchBar:hover {
    border: 2px solid var(--gray-border);
}

.searchBar:focus {
    border: 2px solid var(--sky-blue);
    background-color: #fff;
}

.searchBarIcon {
    display: inline-block;
    background-image: url(/shuan86/oneforall-frontend/static/media/SearchIcon.22902d45.svg);
    width: 40px;
    height: 40px;
    background-repeat: no-repeat;
    background-size: 70% 70%;
    background-position: center center;
    background-color: #fff;
    border: none;
}


.signupButton {
    display: block;
    margin-left: 32px;
    width: 60px;
    height: 40px;
    border-radius: 5px;
    background-color: var(--navy-blue);
    border: none;
    font-size: 16px;
    color: #fff;
    line-height: 40px;
    cursor: pointer;
    transition: background-color .2s ease-in;
}


.signupButton:hover {
    background-color: var(--sky-blue);
}

.signinButton {
    margin-left: 20px;
    width: 60px;
    height: 40px;
    color: var(--black);
    border: 1px solid var(--light-gray);
    background-color: var(--light-gray);
    border-radius: 5px;
    font-size: 16px;
    font-weight: 700;
    line-height: 40px;
    color: var(--black);
    cursor: pointer;
    transition: color .2s ease-in, background-color .2s ease-in;
}

.signinButton:hover {
    background-color: var(--gray);
}

.navDisplayBlock {
    display: block;
}

.navDisplayNone {
    display: none;
}

.navUserInfo {
    display: -webkit-flex;
    display: flex;
    cursor: pointer;
}

.navUserInfoID {
    margin-left: 10px;
    padding: 0 20px;
    line-height: 40px;
}

