@media screen and (max-width: 810px) {
    .navContent {
        -webkit-justify-content: space-between;
                justify-content: space-between;
        padding: 5px;
        position: relative;
    }

    .responsiveNav {
        position: fixed;
        background-color: #fff;
        top: 0px;
        right: 0;
        padding: 10px 0;
        z-index: 1500;
        width: 70vw;
        height: 100vh;
        -webkit-flex-direction: column-reverse;
                flex-direction: column-reverse;
        -webkit-justify-content: flex-end;
                justify-content: flex-end;
        box-shadow: rgb(0 0 0 / 5%) 0 0 7px 5px;
        box-sizing: border-box;
    }

    .navFeature {
        -webkit-flex-direction: column;
                flex-direction: column;
        width: 100%;
    }

    .navUserInfo {
        width: 100%;
        height: 60px;
        -webkit-align-items: center;
                align-items: center;
        padding-left: 15px;
        box-sizing: border-box;
        display: none;
    }

    .signupButton,
    .signinButton {
        font-size: 18px;
        width: 100%;
        text-align: start;
        padding-left: 15px;
        height: 60px;
        margin-left: 0;
        color: var(--black);
        background-color: var(--white);
        transition: none;
        border-radius: 5px;
        border: 0;
    }

    .signinButton {
        color: var(--deep-blue);
    }

    .signupButton:hover,
    .signinButton:hover {
        background-color: var(--light-gray);
    }

    .rankingTable {
        display: none;
    }

    .navUserInfo img {
        display: none;
    }

    .navUserInfoID {
        font-size: 1.25rem;
        padding: 0;
        margin-left: 0;
    }

    .searchBarContainer {
        height: 60px;
        -webkit-align-items: center;
                align-items: center;
        padding: 0 15px;
    }

    .searchBarIcon {
        display: none;
    }

    .searchBar {
        width: 100%;
        border-radius: 5px;
    }

    .linkList {
        -webkit-flex-direction: column;
                flex-direction: column;
        width: 100%;
    }

    .linkList div {
        width: 100%;
        margin: 0;
        text-align: start;
        padding: 0;
        padding-left: 15px;
        box-sizing: border-box;
    }

    .hambergurButton {
        width: 30px;
        height: 30px;
        padding: 10px;
        /* background-color: green; */
    }

    .hambergurButton svg {
        width: 100%;
        height: 100%;
        color: var(--navy-blue);
    }

    .hambergurButton:active svg {
        background-color: var(--light-gray);
        border-radius: 5px;
        -webkit-transform: scale(.9, .9);
                transform: scale(.9, .9);
    }

    .closeNavButton {
        display: block;
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background-color: var(--black-alpha-80);
        transition: opacity 1s;
    }

    .closeNavButton svg {
        display: inline-block;
        position: absolute;
        left: 16px;
        top: 25px;
        border-radius: 5px;
        z-index: 10;
        color: var(--white);
    }

    .closeNavButton:active svg {
        -webkit-transform: scale(.8, .8);
                transform: scale(.8, .8);
    }

    .memberList {
        position: static;
        box-shadow: none;
        width: 100%;
        padding: 0;
        padding-left: 7px;
        box-sizing: border-box;
    }

    .navFeature .phoneDisplay {
        display: block !important;
    }

    .memberListItem {
        padding: 17.5px 0;
        padding-left: 15px;
        -webkit-align-items: center;
                align-items: center;
    }

    .memberListItemTitle {
        font-size: 18px;
    }
}

